.sign-in-box, .account-box {
  position: fixed;
  bottom: 10px;
  left: 10px;
  width: 180px;
  background-color: #ffd139;
  padding: 10px;
  border: 2px solid #947200;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  text-align: center;
}

.sign-in-box input {
  width: 90%;
  margin: 5px 0;
  padding: 5px;
  border: 1px solid #947200;
  border-radius: 5px;
}

.sign-in-box button, .account-box button {
  width: 90%;
  margin: 5px 0;
  padding: 5px;
  background-color: #947200;
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.sign-in-box button:hover, .account-box button:hover {
  background-color: #ffd139;
  color: #947200;
}
